import React, { Component } from 'react'

import { Card, Text, Title } from '../components'

class Pursuits extends Component {

  constructor(props) {
    super(props)

    this.state = { pursuits: null }
  }

  componentDidMount() {
    fetch("pursuits.json", {
      method: "get",
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
    }})
    // .then(resp => {
    //   console.log(resp);
    //   // resp.json()
    // })
    .then(pursuits => this.setState({ pursuits }))
    .catch(err => console.error(err))
  }

  render() {
    const { pursuits } = this.state
    if (!pursuits) return null

    return (
      <div className="row">
        {pursuits.map(p => <PursuitCard {...p} />)}
      </div>
    )
  }
}

const PursuitCard = ({title, body, link}) =>
  <Card className="pursuit col-md-4 col-sm-12">
    <h2 className=".thin">{title}</h2>
    {body.map(el => <PursuitCardSection {...el} />)}
    {link ? <Out link={link} /> : null}
  </Card>

const PursuitCardSection = (props) => {
  if (props.type === "graf") {
    return <Text>{props.text}</Text>
  } else if (props.type === "image") {
    return <img src={props.link} alt={props.alt} style={{ maxWidth: "100%" }} />
  }
}

const Out = ({link}) =>
  <div className="outBtnContainer">
    <a href={link} target="_blank">
      <div class="outBtn">
        <i className="fab chevron-right fa-2x" style={{ color: "black", padding: 50 }}/>
      </div>
    </a></div>

export default Pursuits
